.modal
    background-color: rgba(#2A2C38, .7)
    backdrop-filter: blur(10px)
    @apply absolute inset-0 flex items-center justify-center
    &__content
        @apply max-w-2xl w-full max-h-100 h-full bg-gray rounded-xl flex flex-col
    &__header
        @apply bg-purple p-4 rounded-xl rounded-b-none